.header{
    color: black;
    background: url('../../images/Background\ image_edited.png');
}

.navbar{
    border-color: rgba(255, 255, 255,0.5);
    border-left-width: 0;
    border-right-width: 0;
    border-radius: 0;
    padding-top: 15px;
    padding-bottom: 15px;
}

.transparent{
    background: transparent;
}

.navItem{
    color: white;
    font-family: 'Avenir';
    text-align: center;
    
}

.navItem:hover{
    font-size: 20px;
}

.titleContainer{
    position: relative;
}

.Title{
    position: absolute;
    left: 40px;
    top: 110px;
    color: white;
    font-size: 30px;
    font-weight: 500;
    font-family: 'AvenirBlack';
}



.Subtitle{
    position: absolute;
    left: 40px;
    top: 160px;
    color: white;
    font-size: 28px;
    font-family: 'AvenirLight';
    font-weight: 100;
}


.titleContainer{
    width: 100%;
    height: 400px;
}


@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
    
    .Title{
        font-size: 30px;
    }


    .Subtitle{
        top: 210px;
        font-size: 20px;
    }


    
}


