.TestimonialContainer {
    margin-top: 60px;
    margin-bottom: 20px;
    display: -ms-flexbox;
    display: flex;
    width:100%;
    height: 320px; 
    -ms-flex-pack: center; 
        justify-content: center;  
}

.holder{
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    background: white;
    width: 60%;
    height: inherit;
    border-radius: 10px;
    -webkit-box-shadow: 0px 3px 5px 1px rgba(0, 0, 0, 0.30);
            box-shadow: 0px 3px 5px 1px rgba(0, 0, 0, 0.30)
}

.TTitle {
    color: #4A4A4A;
    font-size: 25px;
    text-align: center;
    margin-top: 30px;
    font-family: "AvenirMedium";
}

.bold{
    font-family: "AvenirBlack";
}

.lineHolder{
    margin-top: 15px;
    -ms-flex: 0.06 1;
        flex: 0.06 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.line {
    background: #205796;
    border-radius: 10px;
    width: 50px;
    height: inherit;
}

.MessageHolder{
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.MessageHolder > img {
    -ms-flex: 0.5 1;
        flex: 0.5 1;
    /* width: 50px; */
    height: 40px;
    margin-top: 40px;
    margin-left: 50px;
}

.MessageHolder > p {
    -ms-flex: 9 1;
        flex: 9 1;
    margin-left: 20px;
    margin-right: 40px;
    margin-top: 30px;
    font-family: "Avenirmedium";
    letter-spacing: 1.4px;
    font-size: 15px;
    line-height: 25px;
}

.ProfileHolder{
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.ProfileHolder > img {
    -ms-flex: 1 1;
        flex: 1 1;
    width: 60px;
    height: 60px;
    border-radius: 35px;
    margin-top: 20px;
    margin-left: 100px;
}

.clientDetails {
    -ms-flex: 100 1;
        flex: 100 1;
    margin-top: 30px;
    margin-left: 20px;
    font-family: "AvenirBlack";
}

.clientName{
    color: #4A4A4A;
    font-size: 18px;
}

.clientJob{
    color: #9B9B9B;
    margin-top: -10px;
}


@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
   .holder{
    width: 90%;
   }

   .MessageHolder > img {
    -ms-flex: 0.5 1;
        flex: 0.5 1;
    /* width: 50px; */
    height: 40px;
    margin-top: 40px;
    margin-left: 20px;
    }

    .TTitle {
        font-size: 15px;
    }

    .lineHolder{
        -ms-flex: 0.05 1;
            flex: 0.05 1;
        
    }

    .MessageHolder{
        -ms-flex:2 1;
            flex:2 1;
    }

    .MessageHolder > p {
        margin-top: 18px;
        line-height: 22px;
        overflow: scroll;
    }

    .TestimonialContainer{
        height: 380px; 
    }

    .ProfileHolder > img {
        margin-top: 0px;
        margin-left: 60px;
    }

    .clientDetails {
        margin-top: 5px;
        margin-left: 20px;
        font-family: "AvenirBlack";
    }
}



